import React from "react";
import "./TalkCard.css";

export default function TalkCard({ talkDetails }) {
  return (
    <div>
      <div class="container">
        <div
          class={talkDetails.isDark ? "dark-rectangle rectangle blog-container" : "rectangle blog-container"}>
         
          <div class="talk-card-title">{talkDetails.title}</div>
          <p className="talk-card-subtitle">{talkDetails.subtitle}</p>

          <div className="card-footer-button-div">
            <a href={talkDetails.slides_url} target="_" class="talk-button">
              Register
            </a>
            <a href={talkDetails.event_url} target="_" class="talk-button">
              Resources
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
